<template>
  <div class="nav-bar">
    <div class="left">
      <img class="logo am-margin-right-sm" src="../assets/img/common/logo.png">
      <i @click="ToggleMenu" class="el-icon-s-fold"></i>
    </div>
    <el-menu mode="horizontal" router background-color="#31404e" text-color="#fff"
             active-text-color="#ffd04b">
      <el-submenu index="1">
        <template slot="title">
          <el-avatar :src="profile.avatar" :size="30"></el-avatar>
          <span class="username am-margin-left-sm">{{ profile.fullname }}</span>
        </template>
        <el-menu-item>消息</el-menu-item>
        <el-menu-item index="/user/info">设置</el-menu-item>
        <el-menu-item @click="handleLogout">退出</el-menu-item>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from 'vuex';

export default {
  computed: {
    ...mapState('User', ['profile']),
    ...mapGetters('Menu', ['toggleIcon']),
  },
  created() {
    // 分发action
    this.$store.dispatch('User/LoadInfo', { id: sessionStorage.id });
  },
  methods: {
    ...mapMutations('Menu', ['ToggleMenu']),
    handleLogout() {
      // 清空sessionStorage
      sessionStorage.clear();
      // 跳转页面
      this.$router.replace('/');
    }
  }
};
</script>

<style lang="less">
.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .left {
    display: flex;
    align-items: center;

    .logo {
      height: 50px;
    }
  }

}
</style>
