import axios from 'axios';

// 添加分类
const add = (formData) => axios.post('/category', formData);
// 删除分类
const remove = (id, params) => axios.delete(`/category/${id}`, { params });
// 更新分类
const update = (id, formData) => axios.put(`/category/${id}`, formData);
// 获取子级分类
const load = (params) => axios.get('/category/sub', { params });

export default {
    add,
    remove,
    update,
    load,
}
