import { Admin } from '@/api/index';

const LoadInfo = async ({ commit, state }, formData) => {
    let { status, data } = await Admin.info(formData);
    if (status) {
        // 储存到state
        commit('SetProfile', data);
    }
    return { status, data };
};

const Update = async ({ commit, state }, formData) => {
    let { status, msg } = await Admin.update(formData);
    if (status) {
        // 储存到state
        commit('SetProfile', formData);
    }
    return { status, msg };
};
export default {
    LoadInfo,
    Update,
}
