import axios from 'axios';

// 获取管理员列表
const list = (params) => axios.get('/admins/list', { params });
// 更新管理员个人资料
const update = (formData) => axios.put('/admins/', formData);
// 删除管理员账户
const remove = (id, params) => axios.delete(`/admins/${id}`, { params });
// 登录
const login = (formData) => axios.post('/admins/login', formData);
// 注册
const register = (formData) => axios.post('/admins/register', formData);
// 获取账户详情
const info = (params) => axios.get('/admins', { params });
// 修改本账户信息
const account = (formData) => axios.put('/admins/account', formData);

export default {
    list,
    update,
    remove,
    login,
    register,
    info,
    account,
}
