import axios from 'axios';
//发布商品
const release = (formData) => axios.post('/system/goods', formData);
//删除商品
const remove = (id, params) => axios.delete(`/system/goods/${id}`, { params });
//编辑商品
const update = (id, formData) => axios.put(`/system/goods/${id}`, formData);
//商品列表
const list = (params) => axios.get('/system/goods/list', { params });
//商品详情
const detail = (params) => axios.get('/system/goods', { params });

export default {
    release,
    remove,
    update,
    list,
    detail,
}